.App {
  text-align: center;
}

body{
  background-color: white;
}

a{
  text-decoration: none;
  color: black;
}

.headercontainer{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.headercontainer h1{
  margin: 0;
  padding: 0.5em;
}

.navbarcontainer{
  width: 100%;
  background-color: rgb(100,175,175);
  display: flex;
  justify-content:space-evenly;
  align-items: center;
  position: sticky;
  z-index: 1;
  top:0;
  padding: 0.25em;
}

.navbarcontainer .navbtn:hover img{
  transform: scale(1.15);
  transition: 3s;
}

.navbarcontainer .navbtn:not(:hover) img{
  transform: scale(1);
  transition: 3s;
}

.logocomponent{
  display: flex;
  float: left;
  justify-content: center;
  align-items: center;
  padding: 0.5em;
}

#supportbtn{
  display: flex;
  right: 0;
  margin-left: auto;
  margin-right: auto;
}

.userheaderimg{
  display: flex;
  justify-content: center;
  align-items: center;
  right: 0;
  margin-left: 0;
  padding-right: 1.25em;
}

#menulist{
  z-index: 2;
}

#greeting-message{
  padding-top: 1em;
}

.navtext{
  padding: 0.5em;
}

#discordlogo:hover, .worksnav:hover{
  transform: scale(1.2);
  transition: 3s;
}

#discordlogo:not(:hover), .worksnav:not(:hover){
  transform: scale(1);
  transition: 3s;
}

.phasestagecard:hover{
  cursor: pointer;
  background-color: rgb(100,175,175);
  transition: 2s;
}

.phasestagecard:not(:hover){
  transition: 2s;
}

#loginbox:hover{
  cursor: pointer;
  background-color: rgba(100, 175, 175, 0.25);
  transition: 5s;
}

#loginbox:not(:hover){
  transition: 5s;
}

#footer{
  bottom: 0;
}